


































































































import { Component, Vue, Watch } from "vue-property-decorator";
import api from "@/microApps/openPlatform/api";
import { validateTel } from "@/utils/validate";

@Component
export default class QualificationManagement extends Vue {
  // 手机号码验证
  isValidateTel: any = (rule: any, value: any, callback: Function) => {
    if (value === "") {
      callback();
    } else if (!validateTel(value)) {
      callback(new Error("电话格式错误"));
    } else {
      callback();
    }
  };
  isEdit: boolean = false;
  isEditSave: boolean = false;
  currentStatus: number = 0;
  applyData: any = {};
  fileList: Array<any> = [];
  form: any = {};
  loading: boolean = false;
  rules: any = {
    developerName: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    idCard: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    attachmentUrl: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: ["change", "blur"]
      }
    ],
    industry: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    website: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      },
      { type: "url", message: "请输入正确的网址", trigger: ["blur", "change"] }
    ],
    contactPerson: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    phoneNumber: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      },
      { validator: this.isValidateTel, trigger: ["blul", "change"] }
    ],
    email: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      },
      { type: "email", message: "请输入正确的邮箱地址", trigger: ["blur", "change"] }
    ],
    address: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ]
  };

  // 图片预览
  dialogImageVisible: boolean = false;
  dialogImageUrl: string = "";

  @Watch("fileList")
  handleAttachChange(fileList: Array<any>) {
    if (fileList.length) {
      this.form.attachmentUrl = fileList[0].url;
    } else {
      this.form.attachmentUrl = "";
    }
  }

  created() {
    this.getApplyData();
  }

  // 获取认证的信息
  getApplyData() {
    this.$http.get(api.getQualificationData).then((res: any) => {
      if (res.data.status) {
        this.applyData = res.data.data;
        // 第一次进来状态默认为0
        if (this.applyData.authStatus === null) {
          this.applyData.authStatus = 0;
        }
        if (this.applyData.attachmentUrl) {
          this.fileList = [{ url: this.applyData.attachmentUrl, smallUrl: this.applyData.attachmentUrl }];
        } else {
          this.fileList = [];
        }
        this.currentStatus = this.applyData.authStatus;
        localStorage.setItem("openPlatformAuth", this.applyData.authStatus.toString());
        if (this.currentStatus === 0) {
          this.isEdit = true;
          this.form = JSON.parse(JSON.stringify(this.applyData));
          delete this.form.authStatus;
        } else {
          this.isEdit = false;
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 保存信息
  handleSave() {
    let refs: any = this.$refs.applyForm;
    refs.validate((valid: boolean) => {
      if (valid) {
        this.loading = true;
        this.$http.post(api.saveQualification, this.form).then((res: any) => {
          this.loading = false;
          if (res.data.status) {
            this.$message.success("保存成功");
            this.getApplyData();
          } else {
            this.$message.error(res.data.message);
          }
        });
      } else {
        return false;
      }
    });
  }

  // 提交信息
  handleSubmit() {
    let refs: any = this.$refs.applyForm;
    refs.validate((valid: boolean) => {
      if (valid) {
        this.loading = true;
        this.$http.post(api.submitQualification, this.form).then((res: any) => {
          this.loading = false;
          if (res.data.status) {
            this.$message.success("提交成功");
            this.getApplyData();
          } else {
            this.$message.error(res.data.message);
          }
        });
      } else {
        return false;
      }
    });
  }

  // 取消审核
  handleCancelApply() {
    this.$confirm("确定取消审核？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.cancelQualification + "/" + this.applyData.id).then((res: any) => {
        if (res.data.status) {
          this.$message.success("取消审核成功");
          this.getApplyData();
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }

  // 修改
  handleEdit() {
    this.isEdit = true;
    this.form = JSON.parse(JSON.stringify(this.applyData));
    this.isEditSave = true;
    delete this.form.authStatus;
  }

  // 修改保存
  handleEditSave() {
    let refs: any = this.$refs.applyForm;
    refs.validate((valid: boolean) => {
      if (valid) {
        this.loading = true;
        this.$http.post(api.editQualification, this.form).then((res: any) => {
          this.loading = false;
          if (res.data.status) {
            this.isEditSave = false;
            this.$message.success("修改成功");
            this.getApplyData();
          } else {
            this.$message.error(res.data.message);
          }
        });
      } else {
        return false;
      }
    });
  }

  // 图片预览
  handlePreview(url: string) {
    this.dialogImageUrl = url;
    this.dialogImageVisible = true;
  }
}
