export const validateTel = (tel: string) => {
  let reg = new RegExp("^[1][3-9][0-9]{9}$");
  return reg.test(tel);
};

export const validateUserName = (name: string) => {
  let reg = new RegExp("^(?![0-9]+$)[0-9A-Za-z_-]{6,18}$");
  return reg.test(name);
};

export const validateImage: (file: File) => { status: boolean; message: string } = (file: File) => {
  const isJPEG = file.type === "image/jpeg";
  const isJPG = file.type === "image/jpg";
  const isPNG = file.type === "image/png";
  const isBMP = file.type === "image/bmp";
  const isLt10M = file.size / 1024 / 1024 < 10;

  if (!(isJPEG || isJPG || isPNG || isBMP)) {
    return {
      status: false,
      message: "上传图片必须是/jpg/jpeg/png/bmp格式!"
    };
  }
  if (!isLt10M) {
    return {
      status: false,
      message: "上传图片大小不能超过 10MB!"
    };
  }
  return {
    status: true,
    message: ""
  };
};

// 验证图片基础方法
interface validateConfig {
  maxKbSize?: number;
  formatList?: string[];
}

interface FormatMemoSizeResult {
  value: number;
  unit: "KB" | "MB" | "GB" | "TB";
}

function formatSize(size: number): FormatMemoSizeResult {
  let result: FormatMemoSizeResult = {
    value: size,
    unit: "KB"
  };
  size = size / 1024;
  if (size >= 1) {
    result.value = +size.toFixed(2);
    result.unit = "MB";
    size = size / 1024;
    if (size >= 1) {
      result.value = +size.toFixed(2);
      result.unit = "GB";
      size = size / 1024;
      if (size >= 1) {
        result.value = +size.toFixed(2);
        result.unit = "TB";
      }
    }
  }
  return result;
}

// parseMemoSize（{ value: 10, unit: "MB" }） 把10MB转化成对应KB数
export function parseMemoSize({ value, unit }: FormatMemoSizeResult): number {
  switch (unit) {
    case "KB":
      return Math.floor(value);
    case "MB":
      return Math.floor(value * 1024);
    case "GB":
      return Math.floor(value * 1024 * 1024);
    case "TB":
      return Math.floor(value * 1024 * 1024 * 1024);
  }
}

export const validateImg: (file: File, config?: validateConfig) => { status: boolean; message: string } = (file: File, config?: validateConfig) => {
  console.log(file);
  if (config) {
    if (config.formatList && config.formatList.every(format => file.type !== "image/" + format)) {
      return {
        status: false,
        message: `上传图片必须是/${config.formatList.join("/")}格式!`
      };
    }
    if (config.maxKbSize && file.size / 1024 > config.maxKbSize) {
      let formatResult = formatSize(config.maxKbSize);
      return {
        status: false,
        message: `上传图片大小不能超过 ${formatResult.value}${formatResult.unit}!`
      };
    }
  }
  return {
    status: true,
    message: ""
  };
};
